*{
    box-sizing: border-box;
}

body {
    font-family: "inter";
    margin: 0;
    height: 100vh;
    background-color: #282D35;

}

nav {
    display: flex;
    align-items: center;
    background-color: #21222A;
    height: 90px;
    padding: 30px 38px;
}

.navbar-logo {
    width: 35px;
    margin-right: 10px;
}
.navbar-logo:hover {
    filter: invert(30%);
}

.navbar-logo-text {
    margin: 0;
    margin-right: auto;
    color: #61DAFB;
    font-size: 22px;
}
.navbar-logo-text:hover, .navbar-title:hover, 
.main-title:hover, .main-facts > li:hover {
    color: #00b7ff;
}

.navbar-title {
    margin: 0;
    color: #DEEBF8;
    font-weight: 600;
}

main {
    padding: 45px 27px;
    color: white;
    line-height: 19px;
}

.main-title {
    margin-left: 17px;
    font-size: 39px;
    letter-spacing: -0.05em;
    line-height: 1.2;
}

.main-facts {
    /* margin-top: 25px; */
}

.main-facts > li {
    line-height: 2;
    padding-block: 10px;
    max-width: 400px;
}

.main-facts > li::marker {
    font-size: 1.4rem;
    color: #61DAFB;
}

footer {
    color: white;
    font-size: 11px;
    font-weight: 300;
    position: absolute;
    width: 100%;
    left: 0;
    bottom: 0;
    text-align: center;
    padding-block: 12px;
}

.footer-icons {
    width: 23px;
    filter: invert(100%);
    margin-right: 13px;
    margin-bottom: 3px;
}

.footer-icons:hover{
    filter: invert(30%);
}

footer > p {
    line-height: .5;
}
